body {
  background-color: #101010;
  font-family: 'Montserrat', sans-serif;
  color: white;
}

.primary-btn {
  background-color: #00ffb9;
  border: 2px solid #00ffb9;
  border-radius: 0;
  color: #101010;
}

.text-prm {
  color: #00ffb9;
}

.nav-link {
  font-size: 16px;
}

.primary-btn:hover {
  background: none;
  border: 2px solid #00ffb9;
  border-radius: 0;
  color: #00ffb9;
  transition-duration: .5s;
}

.secondary-btn {
  background: none;
