}

.work {
  cursor: pointer;
  color: white;
  background-color: #212529 !important;
}
.work:hover {
  background-color: #00ffb9 !important;
  color: #101010 !important;
  transition-duration: .5s;
}

input, textarea{
  border-radius: 0 !important;
}

